import React from "react";
import { Button } from "react-bootstrap";
import searchImg from "../assets/images/search.svg";
import { AppContext } from "../context";
const SearchInput = ({ index, searchValue, activeSearch,onClickActive }) => {
  const { setSearch, setCurrentPage } = React.useContext(AppContext);
  const [isSearch, setIsSearch] = React.useState(false);
  const [input, setInput] = React.useState("");
  const inputRef = React.useRef(null);

  const onClickSearch = () => {
    let searchWord = `&${searchValue}=${input}`;
    setCurrentPage(1);
    window.localStorage.setItem("currentPage",1)
    setIsSearch(false)
    setSearch(searchWord);
  };

  const onClickReset = () => {
    onClickActive(index);
    setIsSearch(!isSearch);
    setInput("");
  };

  return (
    <>
      <div onClick={onClickReset} className="search__icon">
        <img src={searchImg} alt="" />
      </div>
      {index === activeSearch && (
        <div className={isSearch ? `search__item active` : `search__item `}>
          <input
            value={input}
            onChange={(e) => setInput(e.target.value)}
            ref={inputRef}
            type="text"
          />
          <div className="search__buttons">
            <Button onClick={onClickSearch} variant="primary">
              Search
            </Button>
            <Button onClick={onClickReset} variant="secondary">
              Reset
            </Button>
          </div>
        </div>
      )}
    </>
  );
};

export default SearchInput;
