import React, { useState } from "react";
import Container from "react-bootstrap/Container";
import Navbar from "react-bootstrap/Navbar";
import { Link } from "react-router-dom";

import Button from "react-bootstrap/Button";
import Offcanvas from "react-bootstrap/Offcanvas";
import { AppContext } from "../context";

const Header = () => {
  const { isAuth, setIsAuth } = React.useContext(AppContext);
  const [show, setShow] = useState(false);

  const handleClose = () => setShow(false);
  const handleShow = () => setShow(true);

  const onClickLogout = () => {
    window.localStorage.clear();
    setIsAuth(false);
  };
  return (
    <>
      <Navbar bg="light" expand="lg">
        <Container className="p-2">
          <Button variant="primary" onClick={handleShow}>
            
          </Button>

          <Link to="/">
            Charts Panel{" "}
            {isAuth && (
              <Button variant="danger" onClick={onClickLogout}>
                Logout
              </Button>
            )}
          </Link>
        </Container>
      </Navbar>
      <Offcanvas show={show} onHide={handleClose}>
        <Offcanvas.Header closeButton>
          <Offcanvas.Title> </Offcanvas.Title>
        </Offcanvas.Header>
        <Offcanvas.Body className="canvas-item">
          <div>
            <Link onClick={handleClose} className="link" to="/">
                
            </Link>
            <Link onClick={handleClose} className="link" to="/stats">
               
            </Link>
            <Link onClick={handleClose} className="link" to="/info">
                
            </Link>
          </div>
          <p className="about">
            MADE BY: <br /> INTUZA IT ACADEMY
          </p>
        </Offcanvas.Body>
      </Offcanvas>
    </>
  );
};

export default Header;
