import React from "react";
import { Container, Table } from "react-bootstrap";
import { AppContext } from "../context";
import ReactPaginate from "react-paginate";
import SearchInput from "../components/SearchInput";
const TablePage = () => {
  const { users, setCurrentPage, currentPage, pageCount, search, setSearch } =
    React.useContext(AppContext);
  const [activeSearch, setActiveSearch] = React.useState(1);
  const handlePageClick = (e) => {
    setCurrentPage(e.selected + 1);
    window.localStorage.setItem("currentPage", e.selected + 1);
  };

  const dates = users.map((user) => {
  
    return new Date(user.created_at).toLocaleString("ru-RU",{timeZone:"UTC"});
  });

  const onClickBack = () => {
    setSearch("");
  };

  //${dates[i].getHours()}:${dates[i].getMinutes()} ${dates[i].getDate()}.${dates[i].getMonth()}.${dates[i].getFullYear()}
  return (
    <Container>
      <h2> </h2>
      <Table striped bordered hover>
        <thead>
          <tr>
            <th style={{ cursor: "pointer" }} onClick={onClickBack}>
              {search.length > 1 ? "" : "#"}
            </th>
            <th>
              <div className="search">
                <span>...</span>{" "}
                <SearchInput
                  onClickActive={(i) => setActiveSearch(i)}
                  index={1}
                  activeSearch={activeSearch}
                  searchValue="name"
                />
              </div>
            </th>
            <th>
              <div className="search">
                <span> </span>{" "}
                <SearchInput
                  onClickActive={(i) => setActiveSearch(i)}
                  index={2}
                  searchValue="phone"
                  activeSearch={activeSearch}
                />
              </div>
            </th>
            <th>
              <div className="search">
                <span></span>{" "}
                <SearchInput
                  onClick={() => setActiveSearch(3)}
                  onClickActive={(i) => setActiveSearch(i)}
                  index={3}
                  searchValue="city"
                  activeSearch={activeSearch}
                />
              </div>
            </th>
            <th></th>
          </tr>
        </thead>
        <tbody>
          {users.length > 0 ? (
            users.map((user, i) => (
              <tr key={user.id}>
                <td>{(currentPage - 1) * 10 + i + 1}</td>
                <td>{user.name}</td>
                <td>{user.phone}</td>
                <td>{user.city}</td>
                <td>{dates[i]}</td>
              </tr>
            ))
          ) : (
            <td colSpan={5}>
              <h2>!   .</h2>
            </td>
          )}
        </tbody>
      </Table>
      <div className="pag">
        <ReactPaginate
          breakLabel="..."
          nextLabel=" >"
          onPageChange={handlePageClick}
          pageRangeDisplayed={5}
          pageCount={pageCount}
          forcePage={currentPage - 1}
          previousLabel="< "
          renderOnZeroPageCount={null}
        />
      </div>
    </Container>
  );
};

export default TablePage;
