import React from "react";
import { Col, Container, Row, Tooltip } from "react-bootstrap";
import { Funnel, FunnelChart, ResponsiveContainer } from "recharts";
import axios from "../axios";
import SpinnerDiv from "../components/SpinnerDiv";
const InfoPage = () => {
  const [voronka, setVoronka] = React.useState({});
  const fetchData = async () => {
    await axios.get("/info").then((res) => {
      setVoronka(res.data.voronka);
    });
  };
  React.useEffect(() => {
    fetchData();
  }, []);

  const voronkaData = [
    {
      value: voronka.start,
      name: "Start basqanlar",
      fill: "#82ca9d",
    },
    {
      value: voronka.signed,
      name: "Dizimnen tkenler",
      fill: "#FF8042",
    },
    {
      value: voronka.group,
      name: "Gruppaa aza bolanlar",
      fill: "#8dd1e1",
    },
  ];
  if (!voronka) {
    return <SpinnerDiv />;
  }
  return (
    <Container>
      <h2></h2>
      <Row className="justify-content-center">
        <Col md={6} lg={5} style={{ minHeight: "300px" }}>
          <ResponsiveContainer width="100%" height="100%">
            <FunnelChart width={420} height={250}>
              <Tooltip />
              <Funnel
                dataKey="value"
                data={voronkaData}
                isAnimationActive
              ></Funnel>
            </FunnelChart>
          </ResponsiveContainer>
        </Col>
        <Col md={6} lg={5}>
          <div className="vor">
            <div className="vor__info">
              <span style={{ backgroundColor: "#82CA9D" }}></span>
              <p>Start basqanlar - {voronka.start}</p>
            </div>
            <div className="vor__info">
              <span style={{ backgroundColor: "#FF8042" }}></span>
              <p>Dizimnen tkenler - {voronka.signed}</p>
            </div>
            <div className="vor__info">
              <span style={{ backgroundColor: "#8dd1e1" }}></span>
              <p>Gruppaa aza bolanlar - {voronka.group}</p>
            </div>
          </div>
        </Col>
      </Row>
    </Container>
  );
};

export default InfoPage;
