import React from "react";
import { Col, Container, Row, Table } from "react-bootstrap";
import axios from "../axios";

import {
  Cell, Pie,
  PieChart, Tooltip
} from "recharts";
import SpinnerDiv from "../components/SpinnerDiv";

const StatsPage = () => {
  const [datas, setDatas] = React.useState([]);

  const fetchData = async () => {
    await axios.get("/info").then((res) => {
      setDatas(res.data.leads);
    });
  };

  React.useEffect(() => {
    fetchData();
  }, []);

  const data = React.useMemo(() => {
    return datas.map((item) => {
      return {
        name: item.city,
        users: item.users,
      };
    });
  }, [datas]);

  if (datas.length <= 0) {
    return <SpinnerDiv />;
  }

  const COLORS = ["#0088FE", "#00C49F", "#FFBB28", "#FF8042"];
  return (
    <Container style={{ textAlign: "center" }}>
      <h2></h2>
      <Row>
        <Col md={6} lg={4}>
          <div>
            <PieChart className="chart" width={400} height={400}>
              <Pie
                dataKey="users"
                isAnimationActive={false}
                data={data}
                cx={200}
                cy={200}
                outerRadius={80}
                fill="#8884d8"
                label
                paddingAngle={5}
              >
                {data.map((entry, index) => (
                  <Cell
                    key={`cell-${index}`}
                    fill={COLORS[index % COLORS.length]}
                  />
                ))}
              </Pie>
              <Tooltip />
            </PieChart>
          </div>
        </Col>
        <Col className="stats-table" md={6} lg={8}>
          <Table striped bordered hover>
            <thead>
              <tr>
                <th>#</th>
                <th></th>
                <th></th>
              </tr>
            </thead>
            <tbody>
              {data.map((user, i) => (
                <tr key={i}>
                  <td>{i + 1}</td>
                  <td>{user.name}</td>
                  <td>{user.users}</td>
                </tr>
              ))}
            </tbody>
          </Table>
        </Col>
      </Row>
    </Container>
  );
};

export default StatsPage;
