import React from "react";
import { Container, Button, Form } from "react-bootstrap";
import { AppContext } from "../context";
import axios from "../axios";

const LoginPage = () => {
  const { setIsAuth } = React.useContext(AppContext);
  const [user, setUser] = React.useState("");
  const [password, setPassword] = React.useState("");

  const isUserRegistrated = async (fd) => {
    await axios
      .post("/login", fd)
      .then((res) => {
        window.localStorage.setItem("token", res.data.token);
        window.localStorage.setItem("isReg", true);
        setIsAuth(true);
      })
      .catch((error) => alert(error));
  };

  const onRegistration = (e) => {
    e.preventDefault();
    const fd = new FormData();
    fd.append("phone", user);
    fd.append("password", password);
    isUserRegistrated(fd);
  };

  return (
    <Container className="login">
      <Form>
        <Form.Group className="mb-3" controlId="formBasicEmail">
          <Form.Label></Form.Label>
          <Form.Control
            value={user}
            onChange={(e) => setUser(e.target.value)}
            type="text"
            placeholder="Enter login"
          />
        </Form.Group>
        <Form.Group className="mb-3" controlId="formBasicPassword">
          <Form.Label></Form.Label>
          <Form.Control
            value={password}
            onChange={(e) => setPassword(e.target.value)}
            type="password"
            placeholder="Password"
          />
        </Form.Group>
        <Button onClick={onRegistration} variant="primary" type="submit">
          Kiriw
        </Button>
      </Form>
    </Container>
  );
};

export default LoginPage;
