import Header from "./components/Header";
import TablePage from "./pages/TablePage";
import { Routes, Route } from "react-router-dom";
import React from "react";
import axios from "./axios";
import { AppContext } from "./context";
import LoginPage from "./pages/LoginPage";
import SpinnerDiv from "./components/SpinnerDiv";
import StatsPage from "./pages/StatsPage";
import InfoPage from "./pages/InfoPage";

function App() {
  const [isAuth, setIsAuth] = React.useState(false);
  const [users, setUsers] = React.useState([]);
  const [search, setSearch] = React.useState("");
  const [pageCount, setPageCount] = React.useState(0);
  const [currentPage, setCurrentPage] = React.useState(1);

  const fetchData = async (currentPage = 1) => {
    await axios
      .get(`/leads?page=${currentPage}${search}`)
      .then((res) => {
        setIsAuth(true);
        setUsers(res.data.leads.data);
        const totalPage = Math.ceil(Number(res.data.leads.total) / 10);
        setPageCount(totalPage);
      })
      .catch((error) => {
        window.localStorage.setItem("currentPage", 1);
      });
  };

  React.useEffect(() => {
    if (window.localStorage.getItem("currentPage")) {
      setCurrentPage(window.localStorage.getItem("currentPage"));
      fetchData(window.localStorage.getItem("currentPage"));
    } else {
      fetchData();
    }
  }, [currentPage, search, isAuth]);

  return (
    <AppContext.Provider
      value={{
        isAuth,
        setIsAuth,
        users,
        setCurrentPage,
        currentPage,
        pageCount,
        search,
        setSearch,
      }}
    >
      <div className="App">
        {isAuth ? (
          <>
            <Header />
            <Routes>
              <Route path="/" element={<TablePage users={users} />} />
              <Route path="/stats" element={<StatsPage />} />
              <Route path="/info" element={<InfoPage />} />
            </Routes>
          </>
        ) : window.localStorage.getItem("isReg") ? (
          <SpinnerDiv />
        ) : (
          <LoginPage />
        )}
      </div>
    </AppContext.Provider>
  );
}

export default App;
